@media screen and (max-width: 1000px) {
#box_sns #sns_ul {
      margin-left: 70px;
}
}
@media screen and (max-width: 480px) {
#box_sns #sns_ul {
      margin: 0;
}
}
#box_sns #sns_ul .sns_li_icon {
    float: left;
    list-style: none;
    -webkit-transition: all 1s ease 0s;
    transition: all 1s ease 0s;
}
@media screen and (max-width: 8000px) {
#box_sns #sns_ul .sns_li_icon {
        margin: 50px;
}
}
@media screen and (max-width: 800px) {
#box_sns #sns_ul .sns_li_icon {
        margin: 20px;
}
}
@media screen and (max-width: 480px) {
#box_sns #sns_ul .sns_li_icon {
        margin: 25px;
}
}
@media screen and (max-width: 321px) {
#box_sns #sns_ul .sns_li_icon {
        margin: 10px;
}
}
#box_sns #sns_ul .sns_li_icon:nth-child(1):hover {
      opacity: 0.5;
      -webkit-transform: rotateZ(-360deg);
              transform: rotateZ(-360deg);
}
#box_sns #sns_ul .sns_li_icon:nth-child(1):hover .fab {
      -webkit-transform: rotateZ(-360deg);
              transform: rotateZ(-360deg);
}
#box_sns #sns_ul .sns_li_icon:nth-child(2):hover {
      opacity: 0.5;
      -webkit-transform: rotateZ(360deg);
              transform: rotateZ(360deg);
}
#box_sns #sns_ul .sns_li_icon:nth-child(2):hover .fab {
      -webkit-transform: rotateZ(360deg);
              transform: rotateZ(360deg);
}
#box_sns #sns_ul .sns_li_icon:nth-child(3):hover {
      opacity: 0.5;
      -webkit-transform: rotateX(360deg);
              transform: rotateX(360deg);
}
#box_sns #sns_ul .sns_li_icon:nth-child(3):hover .fab {
      -webkit-transform: rotateX(360deg);
              transform: rotateX(360deg);
}
#box_sns #sns_ul .sns_li_icon:nth-child(4):hover {
      opacity: 0.5;
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
}
#box_sns #sns_ul .sns_li_icon:nth-child(4):hover .fab {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
}
#box_sns #sns_ul .sns_li_icon:hover .sns_a {
      color: #fff;
      background-color: #707070;
}
#box_sns #sns_ul .sns_li_icon .sns_a {
      display: block;
      width: 4em;
      padding: 15px 20px;
      border: solid #999 1px;
      border-radius: 5px;
      color: #707070;
      text-align: center;
      -webkit-box-shadow: 1px 1px 5px 1px #999;
              box-shadow: 1px 1px 5px 1px #999;
}
#box_sns #sns_ul .sns_li_icon .sns_a .fab {
        -webkit-transition: all 1s ease 0s;
        transition: all 1s ease 0s;
}
